import React, { Component } from 'react';
import WaveSurfer from 'wavesurfer.js';
import style from "./waveform.module.css"

class Waveform extends Component {  
  state = {
    playing: false,
  };

  componentDidMount() {
    this.waveform = WaveSurfer.create({
      container: '#waveform',
      barWidth: 2,
      progressColor: 'blue',
      responsive: true,
      cursorColor: 'transparent',
    });

    this.waveform.load(this.props.url);
  };
  
  handlePlay = () => {
    this.setState({ playing: !this.state.playing });
    this.waveform.playPause();
  };
  
  render() {
    return (
      <div className={style.WaveformContainer}>     
       <button onClick={this.handlePlay} className={style.PlayButton}>
          {!this.state.playing ? 'Play' : 'Pause'}
        </button> 
        <audio  src={this.props.url}/>
        <div id="waveform" className={style.Wave}/>
      </div>
    );
  }
};

export default Waveform;